import React, {useState, useEffect} from 'react';
import './Home.css'
import { Icon32HashtagOutline } from '@vkontakte/icons';
import md5 from 'md5'
import sha1 from 'sha1'
import sha256 from 'sha256'
import sha512 from 'sha512'

import {Panel, Card, Input, Button, Group, Header, Placeholder} from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import bridge from '@vkontakte/vk-bridge'

const Home = ({ id, go, fetchedUser }) => {
	const [history, setHistory] = useState(<Placeholder
		icon={<Icon32HashtagOutline width={56} height={56}/>}
	  >
Здесь появтся ваши зашифрованые сообщения.
	  </Placeholder>)
	const [value, setValue] = useState(window.location.hash.slice(1,window.location.hash.length))


	const showBanner = () => {
		let x = Math.round(Math.random() * 3)
		if (x = 2) {
		  bridge.sendPromise('VKWebAppShowNativeAds', { ad_format: 'interstitial' })
		}
	  }

	  
	const setMd5 = async () => {
		let hash = await md5(document.getElementById('home-input').value)
		setHistory(
		<Card size="l" mode="shadow" style={{margin: 10, padding: 5}}>
			<Header mode="secondary">Метод - MD5</Header>
			<Group mode="plain" header={<Header mode="secondary">Строка:</Header>}>
			<p className='string'>{document.getElementById('home-input').value}</p>
			</Group>
			<Group mode="plain" header={<Header mode="secondary">Результат:</Header>}>
			<p className='hash'>{hash}</p>
			</Group>
		</Card>
		)
		showBanner()
	}

	const setSha1 = async () => {
		let hash = await sha1(document.getElementById('home-input').value)
		setHistory(
			<Card size="l" mode="shadow" style={{margin: 10, padding: 5}}>
			<Header mode="secondary">Метод - SHA1</Header>
			<Group mode="plain" header={<Header mode="secondary">Строка:</Header>}>
			<p className='string'>{document.getElementById('home-input').value}</p>
			</Group>
			<Group mode="plain" header={<Header mode="secondary">Результат:</Header>}>
			<p className='hash'>{hash}</p>
			</Group>
		</Card>
			)
			showBanner()
	}
	const setSha256 = async () => {
		let hash = await sha256(document.getElementById('home-input').value)
		setHistory(
			<Card size="l" mode="shadow" style={{margin: 10, padding: 5}}>
			<Header mode="secondary">Метод - SHA256</Header>
			<Group mode="plain" header={<Header mode="secondary">Строка:</Header>}>
			<p className='string'>{document.getElementById('home-input').value}</p>
			</Group>
			<Group mode="plain" header={<Header mode="secondary">Результат:</Header>}>
			<p className='hash'>{hash}</p>
			</Group>
		</Card>
			)
			showBanner()
	}
	const setSha512 = async () => {
		let hash = await sha512(document.getElementById('home-input').value)
		setHistory(
			<Card size="l" mode="shadow" style={{margin: 10, padding: 5}}>
			<Header mode="secondary">Метод - SHA512</Header>
			<Group mode="plain" header={<Header mode="secondary">Строка:</Header>}>
			<p className='string'>{document.getElementById('home-input').value}</p>
			</Group>
			<Group mode="plain" header={<Header mode="secondary">Результат:</Header>}>
			<p className='hash'>{hash.toString('hex')}</p>
			</Group>
		</Card>
			)
			showBanner()
	}

	return (

		<Panel id={id}>
			<PanelHeader>Simple hash generator</PanelHeader>
			<Card mode="shadow" style={{margin: 5, padding: 5, fontSize: 14, textAlign: 'center'}}>
				<div >
На этой странице вы можете зашивровать сообщение доступными алгоритмами.
В таких сообщения невозможна подмена, а так же их нельзя подменить.
				</div>
			</Card >
			<Input 
			value={value}
			onChange={() => {
				setValue(document.getElementById('home-input').value)
			}}
			style={{margin: 5}} 
			type="text" 
			placeholder="Строка, которую вы хотите зашифровать" 
			id='home-input'/>
			<div className='home-buttons'>
				<Button className='button-1' onClick={setMd5}>MD5</Button>
				<Button className='button-2' onClick={setSha1}>SHA1</Button>
				<Button className='button-3' onClick={setSha256}>SHA256</Button>
				<Button className='button-4'onClick={setSha512}>SHA512</Button>
			</div>
			<Group>
			<Header mode="secondary">Ваше последдне сообщение</Header>
			{history}
			</Group>
		</Panel>
	);
} 


export default Home;
